<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .header {
        .input {
            min-width: 150px;
            max-width: 70%;
        }
        .header-col {
            height: 60px;
        }
        .header-col-select {
            width: 80%;
        }
    }

    .input {
        min-width: 150px;
        max-width: 70%;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }

</style>

<template>
    <div class="page">
        <div>
            <el-row class="header">
                <el-col class="filter-label" :span="2">规则编号:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="data.id" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2"> 状态:</el-col>
                <el-col :span="4">
                    <el-select class="filter-match" v-model="data.status" placeholder="请选择">
                        <el-option
                                v-for="item in statusOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col class="filter-label" :span="2">动态标题:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="data.title" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-btn" :span="6">
                    <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                    <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                    <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
                </el-col>
            </el-row>

            <el-row class="filter">
                <el-col class="filter-label" :span="2"> 栏目:</el-col>
                <el-col :span="4">
                    <el-select class="filter-match" v-model="data.column_id" placeholder="请选择">
                        <el-option
                                v-for="item in columns"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-col>

                <el-col class="filter-label" :span="2"> 创建日期:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="data.create_time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>

                <el-col class="filter-label" :span="2"> 发布日期:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="data.publish_time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>

                <el-col class="filter-label" :span="2"> 更新日期:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="data.update_time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>

            </el-row>
            <el-row class="filter">
                <el-col class="filter-label" :span="2"> 推荐:</el-col>
                <el-col :span="4">
                    <el-select class="filter-match" v-model="data.recommend" placeholder="请选择">
                        <el-option
                                :value="1"
                                label="推荐">
                        </el-option>
                        <el-option
                                :value="0"
                                label="不推荐">
                        </el-option>
                    </el-select>
                </el-col>

                <el-col class="filter-label" :span="2">发布人:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="data.publisher" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2">动态简介:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="data.description" placeholder="请输入"></el-input>
                </el-col>

            </el-row>

        </div>
        <el-table
                :data="res.data"
                stripe
                style="width: 100%">

            <el-table-column
                    prop="title"
                    label="标题">
            </el-table-column>
            <el-table-column
                    prop="publisher"
                    label="发布人">
            </el-table-column>
            <el-table-column
                    prop="publish_time"
                    label="发布时间">
            </el-table-column>
            <el-table-column
                    prop="update_time"
                    label="更新时间">
            </el-table-column>

            <el-table-column
                    prop="status"
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.status===1?'启用':'未启用'}}</span>
                </template>
            </el-table-column>

            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                    <el-button type="text" size="small" @click="del(scope)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :total="res.last_page"
                    @current-change="loadData"
            >
            </el-pagination>
        </div>

    </div>
</template>

<script>
    import api from '../../js/api/dynamic'
    import columnApi from '../../js/api/column'

    export default {
        name: "dynamic",
        data() {
            return {
                data: {
                    id: null,
                    column_id: null,
                    title: null,
                    description: null,
                    create_start: null,
                    create_end: null,
                    update_start: null,
                    update_end: null,
                    publish_start: null,
                    publish_end: null,
                    recommend: null,
                    status: null,
                    publisher: null,
                    create_time: null,
                    update_time: null,
                    publish_time: null,

                },
                columns: [],
                statusOptions: [{label: '可用', value: 1}, {label: '不可用', value: 0}],
                res: {},
            }
        },

        mounted() {
            this.loadData(1);
            this.loadColumn();
        },

        methods: {
            ref() {
                this.data = {
                    id: null,
                    column_id: null,
                    title: null,
                    description: null,
                    create_start: null,
                    create_end: null,
                    update_start: null,
                    update_end: null,
                    publish_start: null,
                    publish_end: null,
                    recommend: null,
                    status: null,
                    publisher: null,
                    create_time: null,
                    update_time: null,
                    publish_time: null,
                };
                this.loadData(1)
            },
            loadColumn() {
                columnApi.index({page_size: 999}, res => {
                    this.columns=res.data.data.data;
                })
            },
            loadData(page) {
                this.data.jump_page = page;

                if (this.data.create_time != null && this.data.create_time.length === 2) {
                    this.data.create_start = this.data.create_time[0];
                    this.data.create_end = this.data.create_time[1]
                } else {
                    this.data.create_start = null;
                    this.data.create_end = null;
                }
                if (this.data.update_time != null && this.data.update_time.length === 2) {
                    this.data.update_start = this.data.update_time[0];
                    this.data.update_end = this.data.update_time[1]
                } else {
                    this.data.update_start = null;
                    this.data.update_end = null;
                }
                if (this.data.publish_time != null && this.data.publish_time.length === 2) {
                    this.data.publish_start = this.data.publish_time[0];
                    this.data.publish_end = this.data.publish_time[1]
                } else {
                    this.data.publish_start = null;
                    this.data.publish_end = null;
                }
                api.list(this.data, res => {
                    this.res = res.data.data;
                });
            },
            filterOpen() {
                this.data.isOpen = !this.data.isOpen;
            },
            add() {
                this.$router.push({
                    name: "dynamicAdd",
                });
            },
            handleClick(item) {
                this.$router.push({
                    name: "dynamicAdd",
                    query: {
                        id: item.id,
                    },
                });
            },

            del(scope) {
                let _this = this;
                this.$confirm('确认删除' + scope.row.title + '?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.delete({id: scope.row.id}, () => {
                        this.$message.success('删除成功');
                        _this.loadData(1);
                    })
                })
            },
        }
    }
</script>

